html {
  scroll-behavior: smooth;
  transition: all 0.8s;
}

.app {
  text-align: center;
  transition: all 0.6s;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.left-arrow {
  color: white;
  position: fixed;
  top: 5%;
  left: 5%;
}

.right-arrow {
  color: white;
  position: fixed;
  top: 5%;
  right: 5%;
}

.img-back {
  width: 3250px;
  height: 700px;
  overflow-y: hidden;
  max-width: 3250px;
  min-width: 1920px;
  background-repeat: no-repeat;
  transition: ease;
  overflow-x: scroll;
  scroll-behavior: smooth;
  background-image: url(https://odros.com/vFairs/assets/images/fair/hall.jpg);
}

*{
  -ms-overflow-style: none;
}

::-webkit-scrollbar {
  display: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
